import React from 'react';
import logo from './logo.svg';
import { Switch, Route, BrowserRouter, Redirect, Router } from 'react-router-dom';
import './App.css';
import history from './history';
import MainPage from './MainPage';
import DetailContent from './DetailContent';
import UploadPage from './UploadPage';
function App() {
  const isEdge = window.navigator.userAgent.indexOf('Edge') !== -1;
  const isIE = window.navigator.userAgent.indexOf('Trident') !== -1 && !isEdge;
  return isIE ? (
    <div>
      Goodbye, Internet Explorer
    </div> ):(
    <Router history={history}>
      <Switch>
        <Route exact path="/" component={MainPage} />
        <Route exact path="/details" component={DetailContent} />
        <Route exact path="/uploadfile" component={UploadPage} />
      </Switch>
    </Router>
  );
}

export default App;
