import React, { useState, useEffect } from 'react';
import styled from '@emotion/styled';
import { FilePond, File, registerPlugin} from 'react-filepond';
import 'filepond/dist/filepond.min.css'
import FilePondPluginImageExifOrientation from 'filepond-plugin-image-exif-orientation'
import FilePondPluginImagePreview from 'filepond-plugin-image-preview'
import 'filepond-plugin-image-preview/dist/filepond-plugin-image-preview.css'
import {TextField} from '@material-ui/core';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import axios from 'axios';
import Swal from 'sweetalert2';

registerPlugin(FilePondPluginImageExifOrientation, FilePondPluginImagePreview)
const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper,
  },
  gridList: {
    width: '100%',
    height: '100%',
  },
  icon: {
    color: 'rgba(255, 255, 255, 0.54)',
  },
  select: {
    width: '100%'
  },
  searchButton: {
    width: '100%',
    marginTop: 2
  }
  
}));


export default function UploadPage() {
  const classes = useStyles();
  const [files, setFiles] = useState<Array<File>>([]);
  const [id, setId] = useState(0);
  const [title, setTitle] = useState("");
  const [author, setAuthor] = useState("");
  const handleChange = (event: any) => {
    setId(event.target.value);
  }
  const handleSearch = () => {
    console.log(files[0])
    var bodyFormData = new FormData();
    if ( id == 0 || title == "" || author == "" || files.length == 0) {
      Swal.fire("데이터 없음", "데이터가 없습니다", "error")
    } else {
      bodyFormData.set('id', id.toString());
      bodyFormData.append('author', author);
      bodyFormData.append('title', title);
      //or let file = document.querySelector('input[type=file]').files[0];
      bodyFormData.append('multiplefiles', files[0].file)
      axios.post(`http://happycastle.iptime.org:3000/api/upload`, bodyFormData)
      .then(res => {
        Swal.fire("성공","데이터 업로드 성공","info");
      }).catch(err => {
        Swal.fire("통신 실패","서버와의 통신에 실패했습니다","error");
      })
    }
    
  }
  return (
    <MainDiv>
      <SubMainDiv>
        <FormControl variant="outlined" className={classes.select}>
          <InputLabel id="demo-simple-select-outlined-label">학습자료 목록</InputLabel>
          <Select
            labelId="demo-simple-select-outlined-label"
            id="demo-simple-select-outlined"
            value={id}
            onChange={handleChange}
            label="과목"
            className={classes.select}
          >
            <MenuItem value={0}>==선택해주세요==</MenuItem>
            <MenuItem value={1}>화학</MenuItem>
            <MenuItem value={2}>영어</MenuItem>
          </Select>
        </FormControl>
        <TextField
            fullWidth={true}
            label="제목"
            id="standard-basic"
            value={title}
            onChange={(event) => setTitle(event.target.value)}
            />
        <TextField
            fullWidth={true}
            label="업로드 작성자"
            id="standard-basic"
            value={author}
            onChange={(event) => setAuthor(event.target.value)}
            />
        <FilePond
        
        files={files}
        allowMultiple={true}
        onupdatefiles={(files: File[]) => setFiles(files)}
        labelIdle='Drag & Drop your files or <span class="filepond--label-action">Browse</span>'
      />

      <Button variant="contained" color="primary" className={classes.searchButton} onClick={handleSearch}>
        파일 업로드하기
      </Button>
      </SubMainDiv>
      
    </MainDiv>
  );
}

const MainDiv = styled.div`
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
background-color: #ffffff;
min-height: 100vh;
@import url(https://cdn.jsdelivr.net/gh/moonspam/NanumSquare@1.0/nanumsquare.css);
font-family: 'NanumSquare', sans-serif;
`;

const SubMainDiv = styled.div`
  margin: 20px;
  
`;