import React, { useState, useEffect } from 'react';
import styled from '@emotion/styled';
import 'react-toastify/dist/ReactToastify.css';
import { makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import ListSubheader from '@material-ui/core/ListSubheader';
import IconButton from '@material-ui/core/IconButton';
import InfoIcon from '@material-ui/icons/Info';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Button from '@material-ui/core/Button';
import axios from 'axios';
import {Link} from "react-router-dom";
import Swal from 'sweetalert2';

interface TParams { id: number, title: string, author: string, fileurl: string };
const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper,
  },
  gridList: {
    width: '95%',
    height: '100%',
  },
  icon: {
    color: 'rgba(255, 255, 255, 0.54)',
  },
  select: {
    width: '95%'
  },
  searchButton: {
    width: '95%',
    marginTop: 2
  }
  
}));

export default function MainPage() {
  const classes = useStyles();
  const [id, setId] = useState(0);
  const [content, setContent] = useState<Array<TParams>>([
    
  ]);
  const handleChange = (event: any) => {
    setId(event.target.value);
  }
  
  const handleSearch = () => {
    axios.get(`http://happycastle.iptime.org:3000/api/postlist?id=${id}`)
    .then(res => {
      const datas = res.data
      setContent( datas );
      if( res.data.length == 0) {
        console.log("NO SEARCH DATA");
        Swal.fire("데이터 없음","검색 데이터가 없습니다","error");
      }
    })
  }
  return (
    <MainDiv>
      <TitleText>
      대신고등학교 학습자료 공유방
      </TitleText>
      <FormControl variant="outlined" className={classes.select}>
        <InputLabel id="demo-simple-select-outlined-label">학습자료 목록</InputLabel>
        <Select
          labelId="demo-simple-select-outlined-label"
          id="demo-simple-select-outlined"
          value={id}
          onChange={handleChange}
          label="과목"
          className={classes.select}
        >
          <MenuItem value={0}>==선택해주세요==</MenuItem>
          <MenuItem value={1}>화학</MenuItem>
          <MenuItem value={2}>영어</MenuItem>
        </Select>
      </FormControl>
      <Button variant="contained" color="primary" className={classes.searchButton} onClick={handleSearch}>
        검색하기
      </Button>
      <GridList cellHeight={180} className={classes.gridList}>
        <GridListTile key="Subheader" cols={2} style={{ height: 'auto' }}>
          <ListSubheader component="div">검색결과</ListSubheader>
        </GridListTile>
        {content.map((tile) => (
          <GridListTile key={tile.title}>
            <img src={tile.fileurl} alt={tile.title} />
            <GridListTileBar
              title={tile.title}
              subtitle={<span>by: {tile.author}</span>}
              actionIcon={
                <Link<TParams> to={{pathname: "/details", state: tile}} >
                <IconButton aria-label={`info about ${tile.title}`} className={classes.icon}>
                  <InfoIcon />
                </IconButton>
                </Link>
              }
            />
          </GridListTile>
        ))}
      </GridList>
  </MainDiv>
  )
  
}

const MainDiv = styled.div`
display: flex;
flex-direction: column;
align-items: center;
background-color: #ffffff;
min-height: 100vh;
@import url(https://cdn.jsdelivr.net/gh/moonspam/NanumSquare@1.0/nanumsquare.css);
font-family: 'NanumSquare', sans-serif;
`;

const TitleText = styled.text`
  margin-top: 20px;
  font-size: 30px;
  margin-bottom: 20px;
`;
