import React, { useState, useEffect, ReactPropTypes } from 'react';
import styled from '@emotion/styled';
import 'react-toastify/dist/ReactToastify.css';
import { makeStyles } from '@material-ui/core/styles';

import axios from 'axios';
import {
  BrowserRouter as Router,
  Route,
  Link,
  RouteComponentProps,
  LinkProps
} from "react-router-dom";

interface TParams{ id: string, title: string, author: string, fileurl: string };
export default function DetailContent({location}: RouteComponentProps<{}, {}, TParams>) {
  console.log(location.state.title)
  return (
    <MainDiv>
      <TitleText>
        {location.state.title}
      </TitleText>
      <TitleText>
        {location.state.author}
      </TitleText>
      <img src={location.state.fileurl} />
    </MainDiv>
  )
}

const MainDiv = styled.div`
display: flex;
flex-direction: column;
align-items: center;
background-color: #ffffff;
min-height: 100vh;
@import url(https://cdn.jsdelivr.net/gh/moonspam/NanumSquare@1.0/nanumsquare.css);
font-family: 'NanumSquare', sans-serif;
`;

const TitleText = styled.text`
  margin-top: 20px;
  font-size: 30px;
  margin-bottom: 20px;
`;